<template>
    <div class="header">
        <div class="logo"></div>
        <div class="showMusic" id="showMusic" v-on:click="showMusic()"></div>
        <!--<div class="title"> 沙滩音乐</div>-->

    </div>
</template>
<style lang="scss">
    @import "../scss/size";

    .header {
        width: 100%;
        height: px(70);
        background: #000;
        display: flex;
        align-items: center;
        justify-content: space-between;

        color: #fff;
        box-sizing: border-box;
        padding: 0 4% 0 2%;

        .logo {
            /*width: px(30);*/
            width: 12.6rem;
            height: 12.6rem;
            background: url("../assets/logo1.png") no-repeat;
            background-size: contain;
        }
        .showMusic {
            width: px(30);
            height: px(30);
            background: url("../assets/musicshow.png") no-repeat;
            background-size: contain;
        }

    }

</style>
<script>
    export default {
        data() {
            return {}
        },
        methods: {
            //http://m.kugou.com
            showMusic: function () {
                this.$root.$emit("showMain",false);
            }
        }
    }

</script>



